.card-container{
    display: flex;
    flex-direction: column;
    background-color: #95dada;
    border:1px solid grey;
    border-radius: 5px;
    cursor: pointer;
    padding: 25px;
    margin: 1rem;
    transform: translateZ(0);
    
    box-shadow:0 4px 8px 0 rgba(7, 6, 6, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); 
    
}
.card-container h1:hover{
    font-size: 35px;

}
.card-container a:hover{
    font-size: 1.2em;

}